header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 20px;
	flex-shrink: 0;
}

nav {
	display: flex;
	gap: 10px;
	transition: ease all .2s;
}

nav a {
	text-transform: uppercase;
	color: #FF6060;
}

.logo {
	width: 30%;
	max-width: 200px;
	height: auto;
}

@media screen and (min-width: 768px) {
	nav {
		gap: 25px;
	}
}

@media screen and (min-width: 1064px) {
	nav {
		gap: 40px;
	}
}